import React from "react";

const Message = ({ chat, user, time, img }) => (
  <li className={`chat ${user === chat.usernamemina ? "right" : "left"}`}>
    {chat.usernamebot && <img src={img} alt="gotoAndBot" />}
    <div
      className={`usernametime ${
        user === chat.usernamemina ? "right" : "left"
      }`}
    >
      <user className="user">
        {user === chat.usernamemina ? chat.usernamemina : chat.usernamebot}
      </user>
      <time>{time}</time>
    </div>
    {chat.content}
  </li>
  //Message abil kuvame s천numeid.
  //Kui user(Mina) saadab s천numi, siis tema tekst ilmub paremale poole. Kui kirjutab keegi muu, siis vasakule.
  //Samuti, kui parasjagu kirjutanud kasutaja username pole sama mis useri (Mina), siis n채idatakse saatja pilti (ikooni).
  //Kuvame kasutaja nime, aja ning teksti.
);

export default Message;
