import React from "react";
import ReactDOM from "react-dom";
import "./App.css";
import botpic from "./botpic.png";
import Message from "./Message.js";

class Chatroom extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      chats: []
    };

    //this.submitMessage = this.submitMessage.bind(this); ????????
  }

  showTime() {
    var time = new Date();
    return (
      (time.getHours() < 10 ? "0" : "") +
      time.getHours() +
      ":" +
      (time.getMinutes() < 10 ? "0" : "") +
      time.getMinutes()
    );
  }

  componentDidUpdate() {
    this.scrollToBot();
  }

  scrollToBot() {
    ReactDOM.findDOMNode(this.refs.chats).scrollTop = ReactDOM.findDOMNode(
      this.refs.chats
    ).scrollHeight;
  }

  isItMath(message) {
    if (message.includes("calculate")) {
      let letsmath = message.split("calculate").pop();
      return eval(letsmath);
    } else
      return "Kahjuks ei saa aru. \n Arvutamiseks kasutage k채sku calculate.";
  }

  submitMessage(e) {
    e.preventDefault();

    this.setState(
      {
        chats: this.state.chats.concat([
          {
            usernamemina: "Mina",
            content: <p>{ReactDOM.findDOMNode(this.refs.msg).value}</p>,
            time: this.showTime()
          },
          {
            usernamebot: "gotoAndBot",
            content: (
              <p>{this.isItMath(ReactDOM.findDOMNode(this.refs.msg).value)}</p>
            ),
            time: this.showTime()
          }
        ])
      },
      () => {
        ReactDOM.findDOMNode(this.refs.msg).value = "";
      }
    );
  }

  render() {
    const usernamemina = "Mina";
    const { chats } = this.state;

    return (
      <div className="chatroom">
        <header>
          <figure>
            <img src={botpic} alt="botpic.png" />
            <figcaption>gotoAndBot</figcaption>
          </figure>{" "}
        </header>
        <main className="chats" ref="chats">
          {chats.map((chat, index) => (
            <div className="fullcomment" key={index}>
              <Message
                chat={chat}
                user={usernamemina}
                time={chat.time}
                img={botpic}
              />
            </div>
          ))}
        </main>
        <footer>
          <form className="input" onSubmit={e => this.submitMessage(e)}>
            <div className="inputdiv">
              <input type="text" ref="msg" placeholder="Kirjuta siia" />
              <input type="submit" value="&rsaquo;" />
            </div>
          </form>
        </footer>
      </div>
    );
  }
}

export default Chatroom;
